a {
		text-decoration: none;
		color: white;
	}
	
	img {
		width: 100%;
		height: auto;
	}
	
	body {
		padding: 0;
		margin: 0;
		font-family: HelveticaNeueCyr, Arial, sans-serif;
		font-style: normal;
		font-weight: normal;
		font-size: 11px;
		line-height: 14px;
	} 
	
	.wrapper {
		width: 1136px;
		margin: 0 auto;
	}

	.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.slide-btn {
	position: relative;
	box-sizing: border-box;
	display: block;
	width: 34px;
	height: 34px;
	margin-left: 20px;
	border: 1px solid #CACCCF;
	background-color: transparent;
	font-size: 0;
	cursor: pointer;
	&:hover {
		background-color: #186490;
	}
	&:active {
		background-color: #0C4D73;
	}
	&:disabled {
		border: 1px solid #E8E8E8;
	}
}

.slide-btn--next {
	&::before {
		content: url('../../img/svg/arrow_left.svg');
		transform: rotate(180deg);
		position: absolute;
		top: 9px;
		right: 9px;
	}
}

.slide-btn--prev {
	&::before {
		content: url('../../img/svg/arrow_left.svg');
		position: absolute;
		top: 9px;
		right: 9px;
	}
}

.link--blue {
	&:hover {
		color: #186490;
	}
	&:active {
		color: #0C4D73;
	}
}