a {
  text-decoration: none;
  color: white;
}
img {
  width: 100%;
  height: auto;
}
body {
  padding: 0;
  margin: 0;
  font-family: HelveticaNeueCyr, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 14px;
}
.wrapper {
  width: 1136px;
  margin: 0 auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.slide-btn {
  position: relative;
  box-sizing: border-box;
  display: block;
  width: 34px;
  height: 34px;
  margin-left: 20px;
  border: 1px solid #CACCCF;
  background-color: transparent;
  font-size: 0;
  cursor: pointer;
}
.slide-btn:hover {
  background-color: #186490;
}
.slide-btn:active {
  background-color: #0C4D73;
}
.slide-btn:disabled {
  border: 1px solid #E8E8E8;
}
.slide-btn--next::before {
  content: url('../../img/svg/arrow_left.svg');
  transform: rotate(180deg);
  position: absolute;
  top: 9px;
  right: 9px;
}
.slide-btn--prev::before {
  content: url('../../img/svg/arrow_left.svg');
  position: absolute;
  top: 9px;
  right: 9px;
}
.link--blue:hover {
  color: #186490;
}
.link--blue:active {
  color: #0C4D73;
}

.top-contact {
  display: flex;
  align-items: center;
  background-color: #15191D;
  color: #A0A4A8;
  font-weight: normal;
  font-size: 11px;
  line-height: 14px;
  letter-spacing: 0.2px;
  min-height: 40px;
  border-bottom: 1px solid #2A2E32;
}
.top-contact a {
  color: #A0A4A8;
}
.top-contact .wrapper {
  display: flex;
  justify-content: space-between;
}
.top-contact__social {
  text-transform: uppercase;
}
.top-contacts__adres {
  position: relative;
}
.top-contacts__adres::before {
  content: url('../../img/svg/location.svg');
  position: absolute;
  top: 2px;
  left: -15px;
}
.top-contact__phone {
  position: relative;
}
.top-contact__phone::before {
  content: url('../../img/svg/phone.svg');
  position: absolute;
  top: 0;
  left: -15px;
}
.top-contact__working-hours {
  position: relative;
}
.top-contact__working-hours::before {
  content: url(../../img/svg/clock.svg);
  position: absolute;
  top: 0;
  left: -15px;
}
a.visually-impaired {
  color: #F9F9FA;
  text-transform: uppercase;
}

.main-nav {
  display: flex;
  border-left: 1px solid #2a2e32;
  border-right: 1px solid #2a2e32;
}
.main-nav__list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.1px;
}
.main-nav__item {
  padding: 24px 19px;
}
.main-nav__item a {
  cursor: pointer;
}

.slider {
  position: relative;
  min-height: 500px;
  background-color: gray;
}
.slider .wrapper {
  position: relative;
}
.slider__gray-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #15191D;
  opacity: 0.8;
}
.slider__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  max-height: 500px;
}
.slider__text-content {
  position: absolute;
  top: 0;
  left: 0;
  padding-top: 90px;
  width: 630px;
  z-index: 2;
}
.slider__title {
  margin: 0;
  font-size: 52px;
  font-weight: 300;
  line-height: 54px;
  letter-spacing: 0.2px;
  color: #FFFFFF;
}
.slider__description {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.1px;
  color: #CACCCF;
  width: 480px;
}
.slider__text {
  display: inline-block;
  transition: all 0.25s;
}
.slider__button {
  position: relative;
  overflow: hidden;
  display: block;
  padding: 16px 5px;
  width: 210px;
  background: #186490;
  border-radius: 1px;
  text-align: center;
  letter-spacing: 0.2px;
  color: #FFFFFF;
  font-size: 14px;
  line-height: 18px;
  cursor: pointer;
}
.slider__button::after {
  content: url(../../img/svg/arow_right.svg);
  position: absolute;
  top: 17px;
  right: -17px;
}
.slider__button:active {
  background-color: #0C4D73;
}
.slider__button:hover > .slider__text {
  display: inline-block;
  transform: translateX(-50px);
}
.slider__button:hover:after {
  transition: all 0.25s;
  transform: translateX(-30px);
}

.latest-news__section-title {
  font-weight: 900;
  font-size: 32px;
  line-height: 42px;
  letter-spacing: 0.1px;
  color: #15191D;
}
.latest-news__link {
  padding: 10px 15px;
  margin-right: 10px;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.1px;
  color: #52575C;
}
.latest-news__link:hover {
  color: #186490;
}
.latest-news__link--active {
  background-color: #186490;
  color: #FFFFFF;
}
.latest-news__link--active:hover {
  color: #FFFFFF;
}
.latest-news__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 35px;
}
.latest-news__item {
  position: relative;
  width: 557px;
  height: 313px;
  display: flex;
  margin-bottom: 20px;
  flex-direction: column;
  justify-content: flex-end;
  background-color: gray;
  cursor: pointer;
}
.latest-news__item:hover > .blue-layer {
  background-color: rgba(24, 100, 144, 0.9);
}
.latest-news__img {
  position: absolute;
}
.blue-layer {
  min-height: 313px;
  z-index: 1;
}
.latest-news__text-container {
  position: absolute;
  z-index: 2;
}
.latest-news__time {
  padding-left: 30px;
  font-weight: 550;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.1px;
  color: #FFFFFF;
  z-index: 2;
}
.latest-news__title {
  padding-left: 30px;
  font-weight: 300;
  font-size: 26px;
  line-height: 32px;
  letter-spacing: 0.2px;
  color: #FFFFFF;
  z-index: 2;
}

.important-information__title-container {
  background-color: #15191D;
}
.important-information__title-container .wrapper {
  display: flex;
  justify-content: space-between;
}
.important-information__title {
  margin-top: 50px;
  margin-bottom: 50px;
  font-weight: 900;
  font-size: 32px;
  line-height: 42px;
  letter-spacing: 0.1px;
  color: #FFFFFF;
}
.important-information__buttons-container {
  display: flex;
  align-items: center;
}

.navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #15191D;
  color: #ffffff;
  border-bottom: 1px solid #2A2E32;
}
.navigation__container {
  display: flex;
  align-items: center;
}
.navigation__search-icon {
  width: 18px;
  height: 18px;
  margin-right: 20px;
}
.navigation__user-icon {
  width: 18px;
  height: 18px;
}
.navigation__logo-container {
  display: flex;
  align-items: center;
  width: 155px;
}
.navigation .wrapper {
  display: flex;
  justify-content: space-between;
}
.navigation__title {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  width: 125px;
  text-align: right;
  font-style: normal;
  font-weight: 300;
  font-size: 9px;
  line-height: 12px;
  letter-spacing: 0.2px;
}
.navigation__title--footer {
  color: #ffffff;
}
.navigation__logo {
  display: inline-block;
  vertical-align: middle;
  margin-left: 7px;
  width: 27px;
