}

.social-programms {
  padding-top: 110px;
  padding-bottom: 90px;
  background-color: #F9F9FA;
}
.social-programms .wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.social-programms__title {
  margin: 0;
  margin-bottom: 45px;
  font-weight: 900;
  font-size: 32px;
  line-height: 42px;
  letter-spacing: 0.1px;
  color: #15191D;
}
.social-programms__item {
  box-sizing: border-box;
  margin-bottom: 20px;
  padding: 30px;
  width: 557px;
  cursor: pointer;
