.page-footer__copyright {
  display: flex;
  padding: 25px 0;
  justify-content: space-around;
  border-top: 1px solid #2F3236;
}
.page-footer__copyright a {
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.1px;
  color: #52575C;
}
.page-footer__copyright span {
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.1px;
  color: #52575C;
}
.page-footer__serach-input-container {
  position: relative;
}
.page-footer__serach-input-container::before {
  content: url(../../img/svg/search.svg);
  position: absolute;
  top: 17px;
  right: 19px;
}
.page-footer__serach {
  box-sizing: border-box;
  min-height: 50px;
  padding: 16px 42px 12px 18px;
  background-color: transparent;
  border: 1px solid #3A3F44;
  color: #FFFFFF;
}
.page-footer__serach:hover {
  border: 1px solid #186490;
  cursor: pointer;
}
.page-footer__serach:hover::placeholder {
  color: #186490;
}
.page-footer__serach:focus {
  outline: 1px solid #52575C;
  letter-spacing: 0.1px;
  color: #ffffff;
}
