.top-contact {
  display: flex;
  align-items: center;
  background-color: #15191D;
  color: #A0A4A8;
  font-weight: normal;
  font-size: 11px;
  line-height: 14px;
  letter-spacing: 0.2px;
  min-height: 40px;
  border-bottom: 1px solid #2A2E32;
  }
  
  .top-contact a {
    color: #A0A4A8;
  }
  
  .top-contact .wrapper {
    display: flex;
    justify-content: space-between;
  }
  
  .top-contact__social {
    text-transform: uppercase;
  }
  
  .top-contacts__adres {
    position: relative;
  }
  
  .top-contacts__adres::before {
    content: url('../../img/svg/location.svg');
    position: absolute;
    top: 2px;
    left: -15px;;
  }
  
  .top-contact__phone {
    position: relative;
  }
  
  .top-contact__phone::before {
    content: url('../../img/svg/phone.svg');
    position: absolute;
    top: 0;
    left: -15px;
  }
  
  .top-contact__working-hours {
    position: relative;
  }
  
  .top-contact__working-hours::before {
    content: url(../../img/svg/clock.svg);
    position: absolute;
    top: 0;
    left: -15px;
  }
  
  a.visually-impaired {
    color: #F9F9FA;
    text-transform: uppercase;
      
  }