.main-nav {
	display: flex;
	border-left: 1px solid #2a2e32;
	border-right: 1px solid #2a2e32;
}

.main-nav__list {
	display: flex;
	justify-content: space-around;
	align-items: center;
	text-align: center;
	padding: 0;
	margin: 0;
	list-style: none;
	font-size: 14px;
	line-height: 22px;
	letter-spacing: 0.1px;
}

.main-nav__item {
	padding: 24px 19px;
}
	
.main-nav__item a {
	cursor: pointer;
 }
