.slider {
	position: relative;
	min-height: 500px;
	background-color: gray;
}

.slider .wrapper {
	position: relative;
}

.slider__gray-layer {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #15191D;
	opacity: 0.8;
}

.slider__img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	max-height: 500px;
}

.slider__text-content {
	position: absolute;
	top: 0;
	left: 0;
	padding-top: 90px;
	width: 630px;
	z-index: 2;
}

.slider__title {
	margin: 0;
	font-size: 52px;
	font-weight: 300;
	line-height: 54px;
	letter-spacing: 0.2px;
	color: #FFFFFF;
}

.slider__description {
	font-size: 16px;
	line-height: 24px;
	letter-spacing: 0.1px;
	color: #CACCCF;

	width: 480px;
}

.slider__text {
	display: inline-block;
	transition: all 0.25s;
}

.slider__button {
	position: relative;
	overflow: hidden;
	display: block;
	padding: 16px 5px;

	width: 210px;

	background: #186490;
	border-radius: 1px;

	text-align: center;
	letter-spacing: 0.2px;
	color: #FFFFFF;
	font-size: 14px;
	line-height: 18px;
	cursor: pointer;
	&::after {
		content: url(../../img/svg/arow_right.svg);
		position: absolute;
		top: 17px;
		right: -17px;
	}
	&:active {
		background-color: #0C4D73;
	}
	&:hover > .slider__text{
		display: inline-block;
		transform: translateX(-50px);
	}
}

.slider__button:hover:after {
	transition: all 0.25s;
	transform: translateX(-30px);
}