.latest-news__section-title {
	font-weight: 900;
	font-size: 32px;
	line-height: 42px;
	letter-spacing: 0.1px;
	color: #15191D;
}

.latest-news__link {
	padding: 10px 15px;
	margin-right: 10px;
	font-weight: bold;
	font-size: 14px;
	line-height: 18px;
	letter-spacing: 0.1px;
	color: #52575C;
}

.latest-news__link:hover {
	color: #186490;
}

.latest-news__link--active {
	background-color: #186490;
	color: #FFFFFF
}

.latest-news__link--active:hover {
	color: #FFFFFF;
}

.latest-news__container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	margin-top: 35px;
}

.latest-news__item {
	position: relative;
	width: 557px;
	height: 313px;
	display: flex;
	margin-bottom: 20px;
	flex-direction: column;
	justify-content: flex-end;
	background-color: gray;
	cursor: pointer;
}

.latest-news__item:hover > .blue-layer {
	background-color: rgba(24,100,144,0.9);
}

.latest-news__img {
	position: absolute;
}

.blue-layer {
	min-height: 313px;
	z-index: 1;
}

.latest-news__text-container {
	position: absolute;
	z-index: 2;
}

.latest-news__time {
	padding-left: 30px;
	font-weight: 550;
	font-size: 16px;
	line-height: 24px;
	letter-spacing: 0.1px;
	color: #FFFFFF;
	z-index: 2;
}

.latest-news__title {
	padding-left: 30px;
	font-weight: 300;
	font-size: 26px;
	line-height: 32px;
	letter-spacing: 0.2px;
	color: #FFFFFF;
	z-index: 2;
}