.navigation {
	display: flex;
	align-items: center;
	justify-content: space-between;
	background-color: #15191D;
	color: #ffffff;
	border-bottom: 1px solid #2A2E32;
}

.navigation__container {
	display: flex;
	align-items: center;
}

.navigation__search-icon {
	width: 18px;
	height: 18px;
	margin-right: 20px;
}

.navigation__user-icon {
	width: 18px;
	height: 18px;
}

.navigation__logo-container {
	display: flex;
	align-items: center;
	width: 155px;
}

.navigation .wrapper {
	display: flex;
	justify-content: space-between;
}

.navigation__title {
	display: inline-block;
	vertical-align: middle;
	margin: 0;
	width: 125px;
	text-align: right;
	font-style: normal;
	font-weight: 300;
	font-size: 9px;
	line-height: 12px;
	letter-spacing: 0.2px;
}

.navigation__title--footer {
	color: #ffffff;
}

.navigation__logo {
	display: inline-block;
	vertical-align: middle;
	margin-left: 7px;
	width: 27px;
	height: 46px;
}

.navigation__logo-container--footer {
	align-items: flex-start;
	padding-top: 55px;
	padding-right: 10px;
}

